import React from 'react';
import './styles.css';
import ContentLoader from 'react-content-loader';
import CustomeContentLoader from './CustomeContentLoader';

const HocWrappers = (ChildComponent) =>{
    class Component extends React.Component{
      _loadDataArticle2 = async (page=1,totalArticle=5)=> {
        let { dataArticles, pageSize } = this.props;
        console.log("FROM LOAD ARTICKLE 2");
        console.log(this.props);

        let apiKey = '3e6d0f91b9f14b00a5e63f63eb35ee8b';
        this.setState({loading:true, totalArticle:totalArticle});
        const res = await fetch(`https://newsapi.org/v2/top-headlines?country=us&apiKey=${apiKey}&page=${page}&pageSize=${pageSize}`);
        const data = await res.json();
        if (totalArticle>pageSize){
          dataArticles=[...dataArticles,...data.articles];
        }else {
          dataArticles=data.articles;
        }
        console.log(dataArticles);
        // this.setState({dataArticles,loading:false});
      }

      _renderContentLoader(totalArticle){
          return (
            <CustomeContentLoader totalArticle={totalArticle} />
          );
      }

      render(){
        console.log("FROM REDUX");
        console.log(this.props)
        return (
          <ChildComponent 
            {...this.props}
            loadDataArticle2 ={this._loadDataArticle2}
            CustomeContentLoader = {this._renderContentLoader}
          />
        );
      }
    }
    return Component;
}

export default HocWrappers;